* {
    -webkit-font-smoothing: antialiased;
    color: #acbdce;
  }
  
  :root {
    --border-radius: 10px;
  }
  
  
  .Card {
   margin-top: 20px;
    padding: 1px;
    border-radius: 2px;
    background: linear-gradient(-67deg, rgba(200, 216, 231, 0.7), rgba(255, 255, 255, 0.8));
    overflow: hidden;
    box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.6), 2px 2px 12px #c8d8e7;
    width: 380px;
  }
  
  .CardInner {
    padding: 16px 16px;
    background-color: black;
    border-radius: 10px;
  }
  
  .Container {
    display: flex;
  }
  
  .Icon {
      border: solid 2px;
      min-width: 46px;
      min-height: 46px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      margin-left: 12px;
  }
  .Icon svg {
    transform: translate(-1px, -1px);
  }
  
  .CardInner label {
      font-size: 20px;
      font-family: "Hind", sans-serif;
      display: block;
      color: #fff6f6;
      margin-bottom: 12px;
      background: white;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      font-weight: 900;
  }
  
  .InputContainer {
    width: 100%;
  }
  
  .InputContainer input {
     
    background-color: #e3edf7;
    padding: 16px 32px;
    border: none;
    display: block;
    font-family: "Orbitron", sans-serif;
    font-weight: 600;
    color: #a9b8c9;
    -webkit-appearance: none;
    transition: all 240ms ease-out;
    width: 100%;
  }
  .InputContainer input::placeholder {
      font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: #6d7f8f;
  }
  .InputContainer input:focus {
    outline: none;
    color: #6d7f8f;
    background-color: #eff5fa;
  }
  
  .InputContainer {
    --top-shadow: inset 1px 1px 3px #c5d4e3, inset 2px 2px 6px #c5d4e3;
    --bottom-shadow: inset -2px -2px 4px rgba(255,255,255, .7);
    position: relative;
    border-radius: var(--border-radius);
    overflow: hidden;
  }
  .InputContainer:before, .InputContainer:after {
    left: 0;
    top: 0;
    display: block;
    content: "";
    pointer-events: none;
    width: 100%;
    height: 100%;
    position: absolute;
  }
  .InputContainer:before {
    box-shadow: var(--bottom-shadow);
  }
  .InputContainer:after {
    box-shadow: var(--top-shadow);
  }